import React, { useState } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Progress,
  Button,
} from "reactstrap";
import Classes from "./media.module.css";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import { fileType, maxFileSize, selectMaxFile } from "./Funcs";
import axios from "axios";
const Addmedia = (props) => {
  const [loadFile, setLoadFile] = useState([]);

  const changeInputFile = (event) => {
    const files = event.target.files;
    if (
      fileType(event, false) &&
      selectMaxFile(event, false) &&
      maxFileSize(event, false)
    ) {
      const newLoadFile = [...loadFile];
      for (let index = 0; index < files.length; index++) {
        const file = files[index];
        newLoadFile.push({
          file,
          privew: URL.createObjectURL(file),
          loaded: 0,
        });
      }
      setLoadFile(newLoadFile);
    }
  };

  const removeFile = (FileSend) => {
    return setLoadFile(loadFile.filter((File) => File !== FileSend));
  };

  const dragHandler = (event) => {
    event.preventDefault();
  };
  const dragMainHandler = (event) => {
    event.preventDefault();
  };

  const dropHandler = (event) => {
    event.preventDefault();
    const files = event.dataTransfer.files;
    if (
      fileType(event, true) &&
      selectMaxFile(event, true) &&
      maxFileSize(event, true)
    ) {
      const newLoadFile = [...loadFile];
      for (let index = 0; index < files.length; index++) {
        const file = files[index];
        newLoadFile.push({
          file,
          privew: URL.createObjectURL(file),
          loaded: 0,
        });
      }
      setLoadFile(newLoadFile);
    }
  };

  const uploadHandler = () => {
    const tempLoadedFiles = [...loadFile];
    for (let x = 0; x < loadFile.length; x++) {
      if (loadFile[x].loaded !== 100) {
        let data = {
          query: `
                    mutation addmultimedia($image : Upload!) {
                        multimedia(image : $image) {
                          status,
                          message
                        }
                      }`,
          variables: {
            image: null,
          },
        };
        let map = {
          0: ["variables.image"],
        };
        const formD = new FormData();
        formD.append("operations", JSON.stringify(data));
        formD.append("map", JSON.stringify(map));
        formD.append(0, loadFile[x].file, loadFile[x].file.name);

        axios({
          url: "/",
          method: "POST",
          data: formD,
          onUploadProgress: (ProgressEvent) => {
            tempLoadedFiles[x].loaded =
              (ProgressEvent.loaded / ProgressEvent.total) * 100;
          },
        })
          .then((response) => {
            if (response.data.errors) {
              const { message } = response.data.errors[0];
              toast.error(message);
            } else {
              setLoadFile(tempLoadedFiles);
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    }
  };

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>اضافه کردن تصویر جدید</CardHeader>
            <CardBody>
              <div
                className={Classes.addMediaSection}
                onDragOver={dragHandler}
                onDrop={dropHandler}
                onDrag={dragMainHandler}
              >
                <div className={Classes.filePrivew}>
                  {loadFile.map((file, index) => (
                    <div className={Classes.boxPrivew} key={index}>
                      <img src={file.privew} alt={file.privew} />
                      <Progress animated color="success" value={file.loaded}>
                        {file.loaded}%
                      </Progress>
                      {file.loaded === 0 ? (
                        <span
                          className={Classes.remove}
                          onClick={() => removeFile(file)}
                        >
                          <i className="fa fa-remove fa-lg mt-4"></i>
                        </span>
                      ) : null}
                    </div>
                  ))}
                </div>
                <div className={Classes.addFile}>
                  <div className={Classes.label}>
                    <label htmlFor="add_media">گزینش تصویر</label>
                  </div>
                  <div className={Classes.inputFile}>
                    فایل تصویر را بکشید و رها کنید
                    <input
                      type="file"
                      id="add_media"
                      multiple
                      name="add_media"
                      onChange={changeInputFile}
                    />
                  </div>
                </div>
              </div>
            </CardBody>
            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={uploadHandler}
              >
                آپلود
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Addmedia;
