import { toast } from "react-toastify";

export const fileType = (event, dragDrop) => {
  let files;
  if (dragDrop) {
    files = event.dataTransfer.files;
  } else {
    files = event.target.files;
  }

  let error = "";
  const types = ["image/jpeg", "image/jpg", "image/png"];
  for (let index = 0; index < files.length; index++) {
    const file = files[index];
    if (types.every((type) => type !== file.type)) {
      error = "فرمت فایل انتخاب شده صحیح نمی باشد";
    }
  }
  if (error !== "") {
    toast.error(error);
    event.target.value = null;
    return false;
  }
  return true;
};

export const selectMaxFile = (event, dragDrop) => {
  let files;
  if (dragDrop) {
    files = event.dataTransfer.files;
  } else {
    files = event.target.files;
  }
  let error = "";
  if (files.length > 3) {
    error = "تعداد تصاویر بیش از 3 تصویر مجاز نیست";
  }
  if (error !== "") {
    toast.error(error);
    event.target.value = null;
    return false;
  }
  return true;
};

export const maxFileSize = (event, dragDrop) => {
  let files;
  if (dragDrop) {
    files = event.dataTransfer.files;
  } else {
    files = event.target.files;
  }
  let error = "";
  const size = 3000000;
  for (let index = 0; index < files.length; index++) {
    const file = files[index];
    if (file.size > size) {
      error = "حجم تصویر باید کمتر از 3 مگابایت باشد ";
    }
  }
  if (error !== "") {
    toast.error(error);
    event.target.value = null;
    return false;
  }
  return true;
};
